* {
  font-family: Helvetica, Arial, sans-serif;
}
body {
  padding: 60px;
  background-color: #21232A;
}
input {
  width: 100%;
  padding: 10px;
  color: #f9ebff;
  font-size: 18px;
  background-color: #343742;
  border: 0;
  outline: none;
  border-radius: 4px;
}
h3 {
  color: #f9ebff;
  font-size: 30px;
  text-align: center;
  border-bottom: 2px solid #1dd19b;
  padding-bottom: 20px;
}
h1 {
  color: #f9ebff;
}
.loading {
  color: #1dd19b;
  font-size: 20px;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

@media (min-width: 768px) {
  .card-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  align-content: center;
  border-radius: 6px;
  margin-right: 20px; 
  margin-bottom: 20px; 
  overflow: hidden;
  transition: all .3s ease-in-out;
  background-color: #343742;
  color: #f9ebff;
}

.card p  {
  padding: 5px;
  text-align: center;
}
.card img {
   max-height: 200px;
   width: 100%;
}